@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
.main {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .main {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --columns: 4;
  }
}

.card {
