  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  flex-shrink: 0;
  width: 100%;
  padding: 11px 68px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card__rating {
  flex-shrink: 0;
  justify-content: space-between;
  align-items: flex-end;
