  display: flex;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin: 24px 0 16px;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__button {
  color: #00acdc;
  background: #fff;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  flex-shrink: 0;
